html {
  scroll-behavior: smooth;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Cormorant Garamond"), url("../fonts/cormorantgaramond-regular.woff2") format("woff2"), url("../fonts/cormorantgaramond-regular.woff") format("woff");
}
@font-face {
  font-family: "Cormorant Garamond Italic";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: local("Cormorant Garamond Italic"), url("../fonts/cormorantgaramond-italic.woff2") format("woff2"), url("../fonts/cormorantgaramond-italic.woff") format("woff");
}
@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Cormorant Garamond Semi-bold"), url("../fonts/cormorantgaramond-semibold.woff2") format("woff2"), url("../fonts/cormorantgaramond-semibold.woff") format("woff");
}
@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Cormorant Garamond Bold"), url("../fonts/cormorantgaramond-bold.woff2") format("woff2"), url("../fonts/cormorantgaramond-bold.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Poppins"), url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Poppins Medium"), url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Poppins Bold"), url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff");
}
.body {
  min-height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #1D1613;
}

.header {
  position: relative;
}
.header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  top: 114px;
  background-color: #f6f5f4;
}

.header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 45px;
}

.header__top label {
  cursor: pointer;
}
.header__top label svg {
  fill: #1D1613;
  stroke: none;
}

.header__top input {
  padding: 10px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #1D1613;
  border: none;
  border-radius: 0;
  width: 175px;
}
.header__top input::placeholder {
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9B9997;
}
.header__top input:focus {
  outline: none;
  border-bottom: 1px solid #A87B62;
}

.header__nav-toggle {
  display: none;
}

.header__logo {
  position: absolute;
  top: 30px;
  transform: translateX(-33px);
}

.header__user-nav {
  display: flex;
  flex-wrap: wrap;
  padding-top: 11px;
  max-width: 500px;
}

.header__link {
  font-weight: 500;
  font-size: 14px;
  color: #1D1613;
  margin-right: 35px;
}
.header__link:last-child {
  margin-right: 0;
}
.header__link:hover {
  color: #A87B62;
}
.header__link:active {
  opacity: 0.7;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 10px;
}

.header__nav-item {
  padding: 20px 0 20px;
  margin-right: 25px;
}
.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-link {
  font-weight: 500;
  color: #1D1613;
  padding: 5px;
}
.header__nav-link:hover {
  color: #A87B62;
}
.header__nav-link:active {
  opacity: 0.7;
}

.header__tablet-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 25px;
}

.header__tablet-item {
  padding: 10px 0;
}

.header__tablet-item a {
  font-weight: 500;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.75);
}
.header__tablet-item a:hover {
  color: #9B9997;
}
.header__tablet-item a:active {
  opacity: 0.5;
}

.header__tablet-login {
  margin-bottom: 65px;
  padding: 15px 0;
  font-weight: 500;
  font-size: 18px;
  color: #FFFFFF;
}
.header__tablet-login:hover {
  color: #9B9997;
}
.header__tablet-login:active {
  opacity: 0.7;
}

.header__wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding-top: 30px;
  text-align: center;
}

.header__wrapper--active {
  height: 100vh;
  overflow-y: scroll;
  background-color: #A87B62;
}

.header__wrapper svg {
  stroke: #1D1613;
}

.header__wrapper--active svg,
.header__wrapper--active .header__logo svg {
  stroke: #fff;
}

.header__wrapper .header__nav-toggle:hover svg, .header__wrapper .header__nav-toggle:focus svg,
.header__wrapper .header__link:hover svg,
.header__wrapper .header__link:focus svg {
  stroke: #A87B62;
}
.header__wrapper .header__nav-toggle:active svg,
.header__wrapper .header__link:active svg {
  stroke: #9B9997;
}

.header__wrapper--active .header__nav-toggle:hover svg, .header__wrapper--active .header__nav-toggle:focus svg,
.header__wrapper--active .header__link:hover svg,
.header__wrapper--active .header__link:focus svg {
  stroke: #1D1613;
}